.block-choose .block__content {
  display: flex;
  flex-flow: column;
}

.block-choose .block__title {
  margin-bottom: 72px;
}

.block-choose .item-choose {
  flex: 1;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: 20px 0 20px;
}

.block-choose .item-choose h2 {
  line-height: 30px;
}

.block-choose .item-choose h3 {
  font-size: 15px;
  line-height: 20px;
  font-weight: 500;
  color: #616161;
  margin: 0;
}

.block-choose .item-choose:first-child {
  margin-top: 0;
}

.block-choose .item-choose__image {
  max-width: 40px;
  margin-right: 14px;
}

@media only screen and (min-width: 768px) {
  .block-choose .block__content {
    flex-flow: row nowrap;
  }

  .block-choose .item-choose {
    flex-flow: column nowrap;
    align-items: start;
    justify-items: start;
    margin: 0 10px 0 0;
  }

  .block-choose .item-choose:first-child {
    margin-top: initial;
  }

  .block-choose .item-choose:last-child {
    margin-bottom: initial;
  }

  .block-choose .item-choose__image {
    max-width: initial;
    margin-bottom: 20px !important;
  }
}